<template>
	<div class="heroDetail">
		<div class="heroDetail-banner">
			<img :src="skin_url+key+'-'+skins[0].id+'.jpg'">
			<div class="heroDetail-banner-detail">
				<span v-model="heroname">{{herodetail.name}}</span>
				<span>{{hType[tag]}}&nbsp;攻：{{info.attack}}&nbsp;&nbsp;&nbsp;法：{{info.magic}}&nbsp;&nbsp;&nbsp;防：{{info.defense}}&nbsp;&nbsp;&nbsp;操作：{{info.difficulty}}</span>
			</div>
		</div>
		<!-- 技能说明、皮肤欣赏页签切换 -->
		<div class="info-title">
			<div class="skill" @click="skillClick"><a :class="{blue:skillBlue}">技能说明</a></div>
			<div class="skin" @click="skinClick"><a :class="{blue:!skillBlue}">皮肤欣赏</a></div>
		</div>
		<!-- 技能介绍 -->
		<div class="skill-info" v-show="skillBlue">
			<div class="skill-info-tilte"><i></i>技能介绍</div>
			<ul>
				<!-- 被动技能 -->
				<li>
					<div class="hero-icon"><img :src="passive_url+passive_img.full"></div>
					<span>{{passive.name}}</span>
					<p>{{passive.description}}</p>
					
				</li>
				<!-- 主动技能 -->
				<li v-for="skill in skills">
					<div class="hero-icon"><img :src="spell+skill.image.full"></div>
					<span>{{skill.name}}</span>
					<p>{{skill.tooltip}}</p>
				</li>
			</ul>
			<div class="skill-info-tilte"><i></i>技能加点推荐</div>
			<p class="trait" v-if="trait_fz">{{trait_fz}}</p>
			<p class="trait" v-if="trait_zd">{{trait_zd}}</p>
			<p class="trait" v-if="trait_dy">{{trait_dy}}</p>
			<p class="trait" v-if="trait_sd">{{trait_sd}}</p>
			<p class="trait" v-if="trait_adc">{{trait_adc}}</p>
			<br><br><br><br>
		</div>
		<!-- 皮肤欣赏 -->
		<div class="skin-enjoy" v-show="!skillBlue">
			<div class="skin-enjoy-tilte"><i></i>点击皮肤可以观看介绍视频哦</div>
			<ul>
				<li v-for="skin in skins" v-if="skin.name!='默认皮肤'">
					<img :src="skin_url+key+'-'+skin.id+'.jpg'">
					<p>{{skin.name}}</p>
				</li>
			</ul>
				
		</div>
		
	</div>
	
</template>
<style lang="less">
	.blue{
		color: #40a6fb;
		border-bottom: .04rem solid #40a6fb;
	}
	.heroDetail{
		font-size: .3rem;
		overflow: hidden;
		.heroDetail-banner{
			height: 3.7rem;
			position: relative;
			img{
				width: 100%;
				height: 100%;
				display: block;
			}
			.heroDetail-banner-detail{
				width: 100%;
				height: .8rem;
				background: rgba(0,0,0,.3);
				position: absolute;
				bottom: 0;
				font-size: .3rem;
				line-height: .8rem;
				color: #fff;
			}
		}
		i{
			width: .44rem;
			height: .44rem;
			display: block;
			float: left;
			margin: 0 .2rem;
			background: url("../assets/images/info-icon.png") no-repeat;
			background-size: cover;
		}
		.info-title{
			width: 100%;
			/* height: .84rem; */
			overflow: hidden;
			.skill,.skin{
				width: 50%;
				height: .84rem;
				a{
					width: 70%;
					display: block;
					padding: .22rem;
					box-sizing: border-box;
					margin-left: .4rem;
				}
				float: left;
			}
		}
		.skill-info,.skin-enjoy{
			text-align: left;
			>div{
				width: 100%;
				height: .84rem;
				background-color: #f0f0f0;
				padding-top: .2rem;
				box-sizing: border-box;
			}
		}
		.skill-info{
			ul{
				overflow: hidden;
				li{
					width: 100%;
					height: 1.3rem;
					overflow: hidden;
					margin: .25rem 0 0 .3rem;
					float: left;
					.hero-icon{
						width: 1.15rem;
						height: 1.15rem;
						margin-right: .1rem;
						img{
							width: 100%;
							height: 100%;
						}
						float: left;
					}
					p{
						font-size: .24rem;
						color: #666;
						width: 5.7rem;
						overflow: hidden;
						padding-top: .12rem;
					}
				}
			}
		}
		.skin-enjoy{
			ul{
				li{
					width: 100%;
					text-align: center;
					img{
						width: 7.25rem;
						height: 3.55rem;
					}
					p{
						padding: .2rem 0;
					}
				}
			}
		}
		.trait{
			padding: .3rem 0 0 .3rem;
		}
	}
	
</style>
<script type="text/javascript">
	import {mapActions}	from "vuex"
	/*职业类型*/
	const hType = {
	  'Tank': '坦克',
	  'Mage': '法师',
	  'Assassin': '刺客',
	  'Fighter': '战士',
	  'Support': '辅助',
	  'Marksman': '射手'
	}
	/*被动技能URL前缀*/
	const passive_url='http://ossweb-img.qq.com/images/lol/img/passive/'
	/*主动技能URL前缀*/
	const spells='http://ossweb-img.qq.com/images/lol/img/spell/'
	/*皮肤URL前缀*/
	const skin_urls='http://cdn.tgp.qq.com/pallas/images/skins/original/'
	export default {
		created(){
			/*底部隐藏*/
			this.$parent.footerIsshow=false;
			/*this.$parent.heroisBlue=true;
			this.$parent.newsisBlue=this.$parent.playerisBlue=this.$parent.videoisBlue=false;*/
		},
		data(){
		  		return{
		  			herodetail:"",
		  			hType,
		  			passive_url,
		  			spell:spells,
		  			skills:[],
		  			skins:[],
		  			skin_url:skin_urls,
		  			tag:"",
		  			info:"",
		  			passive:"",
		  			passive_img:"",
		  			trait_fz:"",
		  			trait_zd:"",
		  			trait_dy:"",
		  			trait_sd:"",
		  			trait_adc:"",
		  			key:"",
		  			skillBlue:true,
		  			footerIsshow:false,
		  			heroname:""
		  		}
		 },
		 mounted(){
		 	let champion_id=this.$route.params.id;
		 	let heroname=this.$route.params.title;
		 	this.setTitle(heroname);
		 	this.ajax.get('/api/GetChampionDetail?champion_id='+champion_id).then(res=>{
					console.log(res);
					this.herodetail = res.data.data[0];
					this.skills=res.data.data[0].spells;
					this.skins=res.data.data[0].skins;
					this.tag=res.data.data[0].tags[0];
					this.info=res.data.data[0].info;
					this.passive=res.data.data[0].passive;
					this.passive_img=res.data.data[0].passive.image;
					this.key=res.data.data[0].key;
					if(res.data.data[0].trait.fz){
						this.trait_fz="辅助："+res.data.data[0].trait.fz['add-point'];
					}
					if(res.data.data[0].trait.zd){
						this.trait_zd="中单："+res.data.data[0].trait.zd['add-point'];						
					}
					if(res.data.data[0].trait.dy){
						this.trait_dy="打野："+res.data.data[0].trait.dy['add-point'];
					}
					if(res.data.data[0].trait.sd){
						this.trait_sd="上单："+res.data.data[0].trait.sd['add-point'];
					}
					if(res.data.data[0].trait.adc){
						this.trait_adc="ADC："+res.data.data[0].trait.adc['add-point'];
					}
					
				},err=>{
					console.log(err);
				});
		 },
		 methods: {
			...mapActions(["setTitle"]),
			skillClick(){
				this.skillBlue=true;
			},
			skinClick(){
				this.skillBlue=false;
			}
		}
	 }
</script>


