<template>
	<div class="playerPage">
		<div class="playerPage-title">
			<input type="text" v-model="name" placeholder="搜索召唤师">
			<i></i>
			<p @click="jump">搜索</p>
		</div>
		<div class="playerPage-content" v-show="willshow">
			<div></div>
			<p>暂时木有内容呀~</p>
		</div>
		<ul>
			<li v-for="player in players" @click="skip(player)">
				<div class="player-img"><img :src="player.icon_id|headUrl" onerror="onerror=null;src='../assets/images/news-img.jpg'"></div>
				<div class="player-name">
					<a>{{player.name}}</a>
					<p>{{player.area_id|getAreaName}}<span>{{player.level}}</span></p>
				</div>
			</li>
		</ul>
	</div>
</template>
<style lang="less">
	.playerPage{
		width: 100%;
		height: 1.1rem;
		background-color: #40a6fb;
		font-size: .28rem;
		.playerPage-title{
			line-height: 1rem;
			position: relative;;
			input{
				width: 6.6rem;
				height: .7rem;
				border-radius: .05rem;
				padding-left: .35rem;
				font-size: .28rem;	
			}
			i{
				width: .24rem;
				height:.24rem;
				background: url("../assets/images/search.png") no-repeat;
				background-size: cover;
				position: absolute;
				top: .4rem;
				right: 1.2rem;
			}
			p{
				color: #40a6fb;
				position: absolute;
				top: 0;
				right: .6rem;
			}
		}
		.playerPage-content{
			width: 100%;
			div{
				margin: 50% auto 2%;
				width: 1.51rem;
				height: 1.51rem;
				background: url("../assets/images/search-empty.png") no-repeat;
				background-size: cover;
			}
			p{
				
				font-size: .17rem;
			}
		}
		ul{
			li{
				height: 1.6rem;
				background: url("../assets/images/dotted.png") repeat-x;
				background-position: 0 1.6rem;
				overflow: hidden;
				padding-top: .2rem;
				.player-img{
					width: 1rem;
					height: 1rem;
					float: left;
					border-radius: .5rem;
					overflow: hidden;
					margin: .1rem 0 0 .4rem;
					img{
						width: 1rem;
					}
				}
				.player-name{
					width: 6rem;
					text-align: left;
					padding: .1rem 0 0 .2rem;
					float: right;
					box-sizing: border-box;
					line-height: .5rem;
					color: #000;
					a{
						font-size: .3rem;
						font-family: '微软雅黑';
					}
					p{
						font-size: .24rem;
						span{
							padding-left: .3rem;
						}
					}
				}
			}
		}
	}
</style>
<script type="text/javascript">
	import {getAreaName,headUrl} from "../filters"
	import {mapActions}	from "vuex"
	export default {
		created(){
			this.setTitle("玩家");
			this.willshow=true;
			this.$parent.headerIsshow=true;
			this.$parent.footerIsshow=true;
			this.$parent.playerisBlue=true;
			this.$parent.newsisBlue=this.$parent.heroisBlue=this.$parent.videoisBlue=false;
		},
		data(){
		  		return{
		  			name:"",
		  			players:[]
		  		}
		  		
		  },
		methods: {
		  jump () {
			  this.ajax.get('/api/UserArea?keyword='+this.name).then(res=>{
						console.log(res.data.data);
						this.players = res.data.data;
					},err=>{
						console.log(err);
					});
			  this.willshow=false;
		  },
		  skip(player){
		  	this.$router.push({path:`/index/player/summonerDetail/${player.qquin}/${player.area_id}`});
		  },
		  ...mapActions(["setTitle"])
		},
		 filters:{
		  	getAreaName,
		  	headUrl
		 }
	 }
	
</script>


