import Vue from 'vue'
import Router from 'vue-router'
import Landing from '@/views/landing'
import Index from '@/views/index'
import News from '@/views/news'
import Player from '@/views/player'
import Hero from '@/views/hero'
import Video from '@/views/video'
import SummonerDetail from '@/views/summonerDetail'
import FightDetail from '@/views/fightDetail'
import HeroDetail from '@/views/heroDetail'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
    /*  name: 'Hello',*/
      component: Landing
    },
    {
      path: '/index',
   /*   name: 'index',*/
      component: Index,
      children:[
        {path:"news",component: News},
        {path:"player",component: Player},
        {path:"player/summonerDetail/:qquin/:area_id",component: SummonerDetail},
        {path:"player/summonerDetail/:qquin/:area_id/fightDetail/:qquin/:area_id/:gameid",component: FightDetail},
        {path:"hero",component: Hero},
        {path:"hero/heroDetail/:id/:title",component: HeroDetail},
        {path:"video",component: Video}
      ]   
    }
  ]
})



