<template>
	<div>
		<div class="header" v-if="headerIsshow">
			<div class="backBtn" @click="goback"></div>
			<span>{{msg}}</span>
		</div>
		<router-view class="content"></router-view>
		<div class="footer" v-if="footerIsshow">
			<div class="news">
				<img src="../assets/images/news-active.png" v-show="newsisBlue">
				<img src="../assets/images/news.png" v-show="!newsisBlue">
				<router-link to="/index/news" :class="{active:newsisBlue}">资讯</router-link>
			</div>
			<div class="player" href="/index/player">
				<img src="../assets/images/player.png" v-show="!playerisBlue">
				<img src="../assets/images/player-active.png" v-show="playerisBlue">
				<router-link to="/index/player" :class="{active:playerisBlue}">玩家</router-link>
			</div>
			<div class="hero" href="/index/hero">
				<img src="../assets/images/hero.png" v-show="!heroisBlue">
				<img src="../assets/images/hero-active.png" v-show="heroisBlue">
				<router-link to="/index/hero" :class="{active:heroisBlue}">英雄</router-link>
			</div>
			<div class="video" href="/index/video">
				<img src="../assets/images/video.png" v-show="!videoisBlue">
				<img src="../assets/images/video-active.png" v-show="videoisBlue">
				<router-link to="/index/video" :class="{active:videoisBlue}">视频</router-link>
			</div>
		</div>
	</div>
</template>
<style lang="less">
	/* 顶部样式 ----------start*/
	.header{
		width: 100%;
		height: .74rem;
		background-color: #40a6fb;
		padding-top: .2rem;
		box-sizing: border-box;
		font-size: .34rem;
		color: #fff;
		overflow: hidden; 
		.backBtn{
			width: .2rem;
			height: .34rem;
			background: url("../assets/images/backBtn.png") no-repeat;
			background-size: cover;
			margin-left: .33rem;
			float: left;
		}
		position: fixed;
		top: 0;
		z-index: 11;
	}
	
	/* 顶部样式 ----------end*/

	/* 内容样式 ----------start*/
		.content{
			margin: .64rem 0 .8rem;
		}
	/* 内容样式 ----------end*/

	/* 底部样式 ----------start*/
	.footer{
		width: 100%;
		height: .99rem;
		background-color: #fafafa;
		box-shadow: 0 -.1rem .1rem #ddd;
		position: fixed;
		bottom: 0;
		font-size: .2rem;
		color: #6c6c6c;
		overflow: hidden;
		padding-top: .18rem;
		box-sizing: border-box;
		>div{
			width: .6rem;
			float: left;
			margin: 0 .6rem;
			>a{
				color: #6c6c6c;
			}
			>a.active{
				color: #40a6fb;
			}
			>img{
				width: .4rem;
				/* height: .4rem; */
			}
		}
	}
	/* 底部样式 ----------end*/
</style>
<script type="text/javascript">
	import {mapGetters} from "vuex"
	export default{
		created(){
			this.headerIsshow=true;
			this.footerIsshow=true;
			this.newsisBlue=true;
			this.playerisBlue=this.heroisBlue=this.videoisBlue=false;
		},
		computed:{
			...mapGetters(["msg"])
		},
		methods:{
			goback(){
				this.$router.go(-1);
			}
		}
	}
</script>


