<template>
	<div class="summoner-detail">
		<div class="summoner-detail-title">
			<label>{{summoner.name}}</label>
			<div class="headIconBig">
				<img :src="'http://cdn.tgp.qq.com/lol/images/resources/usericon/'+summoner.icon+'.png'">
			</div>
			<label>{{summoner.level}}级</label>
			<p>{{summoner.area_id|getAreaName}}<span>{{tier}}</span></p>
			<p>战斗力：{{summoner.power_value}}<span>赞：{{summoner.praise_num}}</span></p>
		</div>
		<div class="summoner-detail-content">
			<ul>
				<li>
					<img src="../assets/images/triple_kills.png">
					<p>三杀：{{userExt.triple_kills}}</p>
				</li>
				<li>
					<img src="../assets/images/quadra_kills.png">
					<p>四杀：{{userExt.quadra_kills}}</p>
				</li>
				<li>
					<img src="../assets/images/penta_kills.png">
					<p>五杀：{{userExt.penta_kills}}</p>
				</li>

				<li>
					<img src="../assets/images/kills_total.png">
					<p>总击杀：{{userExt.kills_total}}</p>
				</li>
				<li>
					<img src="../assets/images/MVP.png">
					<p>MVP：{{userExtmvp.total_match_mvps}}</p>
				</li>
				<li>
					<img src="../assets/images/god_like_num.png">
					<p>超神：{{userExt.god_like_num}}</p>
				</li>
			</ul>
		</div>
		
		<ul class="fightList">
			<li v-for="battle_list in battle_lists" @click="summonerJump(battle_list)">
				<div class="headIcon"><img :src="battle_list.champion_id|championUrl"></div>
				<div class="ranking">
					<label :class="{green:(battle_list.win==1?false:true),red:(battle_list.win==1?true:false)}">{{battle_list.win==1?"失败":"胜利"}}</label>
					<p>{{GAME_TYPE[battle_list.game_type]}}</p>
				</div>
				<div class="arrow"></div>
			</li>
		</ul>
	</div>
</template>
<style lang="less">
	.green{
		color: #028e0f;
	}
	.red{
		color: #ff0101;
	}
	.summoner-detail{
		.summoner-detail-title{
			width:100%;
			height: 3.6rem;
			background-color: #40a6fb;
			padding-top: 2%;
			box-sizing: border-box;
			color: #fff;
			.headIconBig{
				width:1.3rem;
				height: 1.3rem;
				border-radius: .65rem;
				overflow: hidden;
				margin: .3rem auto;
				img{
					width:1.3rem;
					height: 1.3rem;
				}
			}
			label{
				font-size: .3rem;
				display: block;
			}
			p{
				font-size: .24rem;
				padding-top: .1rem;
				span{
					padding-left: .2rem;
				}
			}
		}
		.summoner-detail-content{
			>ul{
				border-bottom: .01rem solid #ccc;
				overflow: hidden;
				li{
					width: 33%;
					height: 2rem;
					float: left;
					p{
						font-size: .24rem;
					}
					img{
						width: .7rem;
						height: .7rem;
					}
				}
			}
		}
		ul.fightList{
			height: 5.5rem;
			overflow: auto;
			li{
				border-bottom: .01rem solid #ccc;
				width: 100%;
				height: 1.3rem;
				overflow: hidden;
				.headIcon{
					width:1rem;
					height: 1rem;
					border-radius: .5rem;
					overflow: hidden;
					float: left;
					margin: .14rem 0 0 .3rem;
					box-sizing: border-box;
					img{
						width:1rem;
						height: 1rem;
					}
				}
				.ranking{
					width: 5.2rem;
					float: left;
					line-height: .4rem;
					padding-left: .3rem;
					label{
						font-size: .3rem;
						/* color: #ff0101; */
					}
					p{
						font-size: .24rem;
						color: #666;
					}
					text-align: left;
				}
				.arrow{
					width: .26rem;
					height: .38rem;
					background: url("../assets/images/arrow.png") no-repeat;
					background-size: cover;
					float: left;
					margin-top: .46rem;
				}
			}
		}
	}
</style>
<script type="text/javascript">
	import {mapActions}	from "vuex";
	import {getAreaName,championUrl} from "../filters"
	const GAME_TYPES = {
			  '3':'匹配赛',
			  '4': '排位赛',
			  '5': '战队赛',
			  '6': '大乱斗',
			  '7': '人机'
			}
	export default {
		created(){
			this.setTitle("召唤师详情");
			this.$parent.headerIsshow=true;
			this.$parent.footerIsshow=false;
			this.getBattleInfo();
		},
		data(){
			return{
				summoner:"",
				tier:"",
				userExt:"",
				userExtmvp:"",
				battle_lists:[],
				GAME_TYPE:GAME_TYPES,
				flag:true,//用于控制是否可以加载更多
				page: 0//定义当前页数
			}
		},
		 mounted(){
		 	let qquin=this.$route.params.qquin;
			let area_id=this.$route.params.area_id;
			//console.log(qquin);
			//console.log(area_id);
		 	this.ajax.get('/api/UserHotInfo?qquin='+qquin+'&vaid='+area_id).then(res=>{
						//console.log(res);
						this.summoner=res.data.data[0];
						this.ajax.get('/api/GetTierQueue?tier='+this.summoner.tier+'&queue='+this.summoner.queue).then(res=>{
							//console.log(res);
							/*段位*/
							this.tier=res.data.data[0].return;
							
						},err=>{
							console.log(err);
						});
					},err=>{
						console.log(err);
					});
		 	/*用户详情*/
		 	this.ajax.get('/api/UserExtInfo?qquin='+qquin+'&vaid='+area_id).then(res=>{
						//console.log(res);
						this.userExt=res.data.data[1];
						this.userExtmvp=res.data.data[2];
					},err=>{
						console.log(err);
					});
		 	/*排位赛 对战详情*/
		 	/*this.ajax.get('/api/CombatList?qquin='+qquin+'&vaid='+area_id+'&p=4').then(res=>{
						console.log(res);
						this.battle_lists=res.data.data[0].battle_list;
					},err=>{
						console.log(err);
					});*/
			var el = document.querySelector('.fightList');
			el.addEventListener('scroll',(function(){//给比赛添加滚动监听
				if(this.flag){
					this.handler(el);
					let handleFlag = setTimeout((function(){
						this.flag = true;
						clearTimeout(handleFlag);
					}).bind(this),200);
				}
			}).bind(this))

		 },
		 methods: {
			  ...mapActions(["setTitle"]),
			  //跳转
			  summonerJump(battle_list){
			  	let qquin=this.$route.params.qquin;
				let area_id=this.$route.params.area_id;
			  	this.$router.push({path:`/index/player/summonerDetail/${qquin}/${area_id}/fightDetail/${qquin}/${area_id}/${battle_list.game_id}`});

			  },
			  //ajax请求数据
			  getBattleInfo: function(){
				let qquin = this.$route.params.qquin
				let vaid = this.$route.params.area_id
				this.ajax.get("/api/CombatList?qquin="+qquin+"&vaid="+vaid+"&p="+this.page).then(res=>{
					this.battle_lists = this.battle_lists.concat(res.data.data[0].battle_list);//拼接数组
					//console.log(this.battle_lists);
				},err=>{
					console.log(err)
				})
			},
			handler:function(el){
				//获取scrollTop值
				var scrollTop = parseInt(el.scrollTop);
				//获取包含对象的高度
				var scrollHeight = el.scrollHeight;
				//获取可视区域高度
				var offsetHeight = el.offsetHeight;
				//console.log(this)
				if(scrollTop+15 >= scrollHeight - offsetHeight){
					this.page++;
					this.flag = false;
				}
			}

		},
		watch:{//监测page改变调用ajax
			"page": function(){
				this.getBattleInfo()
			}
		},

		filters:{
		  	getAreaName,
		  	championUrl
		 }
	 }
	
</script>


