<template>
	<div class="newsPage">
		<swiper :options="swiperOption">
			<swiper-slide v-for="item in bannerNews">
				<a :href="item.article_url">
					<img width="100%" height="20%" :src="item.image_url_big">
				</a>
			</swiper-slide>
			<div class="swiper-pagination" slot="pagination"></div>
		</swiper>
		<ul>
			<li v-for="obj in newsList">
				<a :href="obj.article_url">
					<div class="news-list-img"><img :src="obj.image_url_small"></div>
					<div class="news-list-content">
						<label>{{obj.title}}</label>
						<p>{{obj.summary}}</p>
						<p>{{obj.publication_date.split(" ")[0]}}<span>{{Math.round(obj.pv/10000)}}万阅读</span></p>
					</div>
				</a>
				
			</li>
		</ul>
	
	</div>
</template>
<style lang="less">
	.newsPage{
		ul{
			overflow: hidden;
			li{
				width: 100%;
				margin: .1rem;
				float: left;
				border-bottom: .01rem solid #f0f0f0;
				overflow: hidden;
				list-style: none;
				.news-list-img{
					float: left;
					img{
						width: 1.5rem;
						height: 1.3rem;
					}
				}
				.news-list-content{
					width: 5.8rem;
					margin-top: -.6rem;
					padding-right: .2rem;
					float: right;
					text-align: left;
					box-sizing: border-box;
					label{
						font-size: .3rem;
						color: #000;
					}
					p{
						font-size: .24rem;
						padding-bottom: .1rem;
						color: #a7a7a7;
					}
					span{
						color: #40a6fb;
						margin-left: .45rem;
					}
				}
			}
		}

		.swiper-container{
			.swiper-pagination-bullet{
				background: #fff;
				opacity: 1;
			}
			.swiper-pagination-bullet-active{
				background-color: #40a6fb;
				opacity: 1;
			}
			
		}
	}

</style>
<script type="text/javascript">
	import { swiper, swiperSlide } from "vue-awesome-swiper"
	import { newsList } from "../data/newslist"
	import { bannerNews } from "../data/banner"
	import {mapActions}	from "vuex"
	export default {
		created(){
			this.setTitle("资讯");
			this.$parent.headerIsshow=true;
			this.$parent.footerIsshow=true;
			this.$parent.newsisBlue=true;
			this.$parent.playerisBlue=this.$parent.heroisBlue=this.$parent.videoisBlue=false;
		},
		data(){
			return {
				swiperOption:{
					pagination:".swiper-pagination",
					paginationClickable:true,
					autoplay:3000
				},
				bannerNews,
				newsList
			}
		},
		components:{
			swiper,
			swiperSlide
		},
		methods:{
			...mapActions(["setTitle"])
		}
	}
</script>


