<template>
	<div class="heroPage">
		<ul>
			<li v-for="hero in heros" @click="heroJump(hero)">
				<a>
				<div class="hero-icon"><img :src="pic+hero.id+'.png'"  onerror="onerror=null;this.src='../assets/images/news-img.jpg'"></div>
					<div class="hero-name"><a>{{hero.title}}</a><p>{{hero.cname}}({{hero.ename}})</p></div>
				</a>
			</li>
			<!-- <li>
				<div class="hero-icon"><img src="../assets/images/news-img.jpg"></div>
				<div class="hero-name"><a>黑暗之女</a><p>安妮(Annie)</p></div>
			</li> -->
		</ul>
	</div>
	
</template>
<style lang="less">
	.heroPage{
		ul{
			height: 14rem;
			overflow: auto;
			li{
				width: 3.3rem;
				height: 1.3rem;
				overflow: hidden;
				margin: .25rem 0 0 .3rem;
				float: left;
				.hero-icon{
					width: 1.15rem;
					height: 1.15rem;
					border-radius: .53rem;
					overflow: hidden;
					img{
						width: 100%;
						height: 100%;
					}
					float: left;
				}
				.hero-name{
					margin-top: -.5rem;
					a{
						font-size: .28rem;
					
					}
					p{
						font-size: .26rem;
						color: #a7a7a7;
					}
				}
			}
		}
	}
	
</style>
<script type="text/javascript">
	const pics="http://cdn.tgp.qq.com/pallas/images/champions_id/";
	import {mapGetters,mapActions}	from "vuex"
	export default {
		created(){
			this.setTitle("英雄");
			/*this.setChampionList();*/
			this.$parent.headerIsshow=true;
			this.$parent.footerIsshow=true;
			this.$parent.heroisBlue=true;
			this.$parent.newsisBlue=this.$parent.playerisBlue=this.$parent.videoisBlue=false;
		},
		data(){
		  		return{
		  			name:"",
		  			heros:[],
		  			items:[],
		  			pic:pics,
		  			flag:true,//用于控制是否可以加载更多
					nowPage: 0//定义当前页数
		  		}
		 },
		computed:{
			/*...mapGetters(["championList"])*/
		},
		 methods: {
			...mapActions(["setTitle"]),
			/*...mapActions(["setChampionList"])*/
			heroJump(hero){
				this.$router.push({path:`/index/hero/heroDetail/${hero.id}/${hero.title}`});
			},

		},
		 mounted(){
		 	this.ajax.get('/api/champion/').then(res=>{
				//console.log(res);
				this.heros =res.data.data;

				//console.log(this.items);
			},err=>{
				console.log(err);
			});
		 	
			/*this.getHeros(this.items);*/
		 },
		
	 }
</script>


