<template>
	<div class="fightDetail">
		<div class="fightDetail-title">开始时间：{{battle.start_time.split(" ")[1].substring(0,5)}}<span>总时长：{{Math.round(battle.duration/60)}}分钟</span><span>模式：匹配赛</span></div>
		
		<ul>
			<li class="against">
				<div class="winner">
					<div class="winner-name">获胜方</div>
					<div class="fight-icons"></div>
				</div>
			</li>
			<li v-for="gamer_record in gamer_records" v-if="gamer_record.win==2">
				<div class="headIcon">
					<img :src="gamer_record.champion_id|championUrl"  onerror="onerror=null;src='../assets/images/news-img.jpg'">
					<div>LV{{gamer_record.level}}</div>
				</div>
				<div class="fight">
					<p>{{gamer_record.name}}</p>
				<!-- 	装备图片 -->
					<div class="skill_icons"><!-- <img :src="skill_icon"> -->
						<img v-if="gamer_record.item0" :src="skill_url+gamer_record.item0+'.png'" onerror="onerror=null;src='../assets/images/3117.png'">
						<img v-if="gamer_record.item1" :src="skill_url+gamer_record.item1+'.png'"  onerror="onerror=null;src='../assets/images/3117.png'">
						<img v-if="gamer_record.item2" :src="skill_url+gamer_record.item2+'.png'"  onerror="onerror=null;src='../assets/images/3117.png'">
						<img v-if="gamer_record.item3" :src="skill_url+gamer_record.item3+'.png'"  onerror="onerror=null;src='../assets/images/3117.png'">
						<img v-if="gamer_record.item4" :src="skill_url+gamer_record.item4+'.png'"  onerror="onerror=null;src='../assets/images/3117.png'">
						<img v-if="gamer_record.item5" :src="skill_url+gamer_record.item5+'.png'"  onerror="onerror=null;src='../assets/images/3117.png'">
						<img v-if="gamer_record.item6" :src="skill_url+gamer_record.item6+'.png'"  onerror="onerror=null;src='../assets/images/3117.png'">
					</div>
					<div class="battle-num">{{gamer_record.champions_killed}}/{{gamer_record.num_deaths}}/{{gamer_record.assists}}</div>
				</div>	
			</li>
			<li class="against">
				<div class="winner">
					<div class="winner-name">失败方</div>
					<div class="fight-icons"></div>
				</div>
			</li>
			<li v-for="gamer_record in gamer_records" v-if="gamer_record.win==1">
				<div class="headIcon">
					<img :src="gamer_record.champion_id|championUrl">
					<div>LV{{gamer_record.level}}</div>
				</div>
				<div class="fight">
					<p>{{gamer_record.name}}</p>
				<!-- 	装备图片 -->
					<div class="skill_icons"><!-- <img :src="skill_icon"> -->
						<img v-if="gamer_record.item0" :src="skill_url+gamer_record.item0+'.png'"  onerror="onerror=null;src='../assets/images/news-img.jpg'">
						<img v-if="gamer_record.item1" :src="skill_url+gamer_record.item1+'.png'"  onerror="onerror=null;src='../assets/images/news-img.jpg'">
						<img v-if="gamer_record.item2" :src="skill_url+gamer_record.item2+'.png'"  onerror="onerror=null;src='../assets/images/news-img.jpg'">
						<img v-if="gamer_record.item3" :src="skill_url+gamer_record.item3+'.png'"  onerror="onerror=null;src='../assets/images/news-img.jpg'">
						<img v-if="gamer_record.item4" :src="skill_url+gamer_record.item4+'.png'"  onerror="onerror=null;src='../assets/images/news-img.jpg'">
						<img v-if="gamer_record.item5" :src="skill_url+gamer_record.item5+'.png'"  onerror="onerror=null;src='../assets/images/news-img.jpg'">
						<img v-if="gamer_record.item6" :src="skill_url+gamer_record.item6+'.png'"  onerror="onerror=null;src='../assets/images/news-img.jpg'">
					</div>
					<div class="battle-num">{{gamer_record.champions_killed}}/{{gamer_record.num_deaths}}/{{gamer_record.assists}}</div>
				</div>	
			</li>
		</ul>
	</div>
	
</template>
<style lang="less">
	.fightDetail{
		font-size: .28rem;
		overflow: hidden;
		.fightDetail-title{
			width: 100%;
			height: .8rem;
			background-color: #f0f0f0;
			line-height: .8rem;
			span{
				padding-left: .4rem;
			}
		}
		.against{
			margin: 0;
		}
		.winner, .loser {
			overflow: hidden;
			padding: .2rem;
			border-bottom: .01rem solid #ccc;
			.winner-name{
				width: 1.56rem;
				height: .58rem;
				background-color: #ff9900;
				color: #fff;
				line-height: .58rem;
				float: left;
			}
			
			.fight-icons{
				width: 1.94rem;
				height: .56rem;
				background: url("../assets/images/icons.png") no-repeat;
				background-size: cover;
				float: right;
			}
		}
		ul{
			li{
				width: 100%;
				overflow: hidden;
				margin: .25rem 0 0 .3rem;
				.headIcon{
					width:  1.26rem;
					height: 1.8rem;
					img{
						width: 1.26rem;
						height: 1.26rem;
						display: block;
					}
					div{
						width: 1.26rem;
						height: .54rem;
						border: .01rem solid #23a723;
						box-sizing: border-box;
						line-height: .54rem;
					}
					float: left;

				}
				.fight{
					width: 5.8rem;
					height: 1.8rem;
					float: left;
					border-bottom: .01rem solid #ccc;
					padding: .2rem .2rem 0 0;
					box-sizing: border-box;
					margin-left: .1rem;
					p{
						width: 3.5rem;
						font-size: .3rem;
						text-align: left;
					}
					.skill_icons{
						width: 4.4rem;
						height: 1rem;
						float: left;
						padding-top: .4rem;
						text-align: left;
						img{
							width: .5rem;
							height: .5rem;
							/* margin-right: .2rem; */
						}
					}
					.battle-num{
						width: 1rem;
						height: 100%;
						line-height: 100%;
						float: right;
						color: #ccc;
					}
				}
				
			}
		}
	}
	
</style>
<script type="text/javascript">
	import {championUrl} from "../filters"
	import {mapActions}	from "vuex"
	const skill_urls="http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/"
	export default {
		created(){
			this.setTitle("对战详情");
			this.$parent.headerIsshow=true;
			this.$parent.footerIsshow=false;
		},
		data(){
		  		return{
		  			name:"",
		  			heros:[],
		  			battle:"",
		  			gamer_records:[],
		  			skill_icon:"",
		  			skill_url:skill_urls
		  		}
		 },
		 mounted(){
		 	let qquin=this.$route.params.qquin;
			let area_id=this.$route.params.area_id;
			let gameid=this.$route.params.gameid;
			//console.log(gameid);
			/*对战详情*/
		 	this.ajax.get('/api/GameDetail?qquin='+qquin+'&vaid='+area_id+'&gameid='+gameid).then(res=>{
						console.log(res);
						this.battle=res.data.data[0].battle;
						this.gamer_records=res.data.data[0].battle.gamer_records;
						for(var i=0;i<this.gamer_records.length;i++){
							for(var j=0;j<7;j++){
								var itemid="item"+j;
								//console.log(itemid);
								this.ajax.get('/api/GetitemIcon?itemid='+this.gamer_records[i][itemid]).then(res=>{
									//console.log(res);
									this.skill_icon=res.data.data[0].return;
									//console.log(this.skill_icon);
								},err=>{
									console.log(err);
								});
							}
							
						}
					},err=>{
						console.log(err);
					});
		 	
		 },
		 methods: {
			
			...mapActions(["setTitle"])
		},
		filters:{
		  	championUrl
		 }
	 }
</script>


