import Vue from "vue"
import Vuex from "vuex"
import axios from 'axios'
axios.defaults.headers.common={"DAIWAN-API-TOKEN":"A2464-49DD6-2BA24-466A5"}
Vue.use(Vuex)

export default new Vuex.Store({
	//状态集合
	state:{
		title:"资讯",
		championList:[],
	},
	//突变
	mutations:{
		SET_TITLE(state,title){
			state.title=title
		},
		SETCHAMPIONLIST(state,list){
			state.championList=list
		},
	},
	//方法集合
	actions:{
		setTitle({commit},title){
			commit("SET_TITLE",title)
		},
		setChampionList:({commit})=>{
			axios.get('/api/champion').then(res=>{
				// this.heros = res.data.data;
				commit('SETCHAMPIONLIST',res.data.data)
				 console.log(res.data.data);
			},err=>{
				console.log(err);
			})
		},
		

	},
	//获取状态
	getters:{
		msg:state=>state.title,
		championList:state => state.championList
	}
})


