<template>
	<div class="videoPage">
		<ul>
			<li v-for="video in videoList">
				<a :href="video.url">
					<img :src="video.img">
					<h1>{{video.title}}</h1>
					<p>{{video.createdate.split("T")[0]}}<span>{{video.comments}}评论</span></p>
				</a>
				
			</li>
			
		</ul>
	</div>
</template>
<style lang="less">
	.videoPage{
		ul{
			overflow: hidden;
			li{
				text-align: left;
				width: 3.3rem;
				overflow: hidden;
				float: left;
				margin: .15rem 0 0 .3rem;
				img{
					width: 3.3rem;
					height: 2.2rem;
				}
				h1{
					font-size: .22rem;
					font-weight: 400;
					height: .6rem;
					color: #000;
				}
				p{
					width: 3.3rem;
					font-size: .18rem;
					color: #a7a7a7;
					span{
						color: #40a6fb;
						float: right;
					}
					padding-top: .05rem;
				}
			}
		}
	}
</style>
<script type="text/javascript">
	import { videoList } from "../data/videolist"
	import {mapActions}	from "vuex"
	export default {
		created(){
			this.setTitle("视频");
			this.$parent.headerIsshow=true;
			this.$parent.footerIsshow=true;
			this.$parent.videoisBlue=true;
			this.$parent.newsisBlue=this.$parent.heroisBlue=this.$parent.playerisBlue=false;
		},
		data(){
			return {
				videoList
			}
		},
		methods:{
			...mapActions(["setTitle"])
		}
	}
</script>


